        <div class="m-topic-hot m-topic-box">
            <div class="m-topic-top">
                <div class="m-topic-top__time">
                    <i :class="getTimeAgo(data.updated_at).icon"></i>
                    <span>{{ getTimeAgo(data.updated_at).text }} </span>
                </div>
                <div class="m-topic-top_right">
                    <div class="u-item">
                        <svg xmlns="http://www.w3.org/2000/svg" width="13.333" height="12" viewBox="0 0 13.333 12" fill="none" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path fill-rule="evenodd" fill="#7A7A7A" d="M4 12c-2.209 0-4-1.797-4-3.996V3.996A3.994 3.994 0 014 0h5.333c2.209 0 4 1.797 4 3.996V12H4zm0-1.333a2.67 2.67 0 01-2.667-2.663V3.996A2.66 2.66 0 014 1.333h5.333A2.67 2.67 0 0112 3.996v6.67H4zm0-4h1.333V5.333H4v1.334zm4 0h1.333V5.333H8v1.334z"/></svg>
                        {{ data.reply_count || 0 }}
                    </div>
                    <div class="u-item">
                        <svg xmlns="http://www.w3.org/2000/svg" width="14.667" height="13.574" viewBox="0 0 14.667 13.574" fill="none" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path fill-rule="evenodd" fill="#7A7A7A" d="M11.887 13.574H.667A.667.667 0 010 12.908V6.24c0-.368.298-.667.667-.667h2.321c.217 0 .42-.105.545-.282L7.168.142A.333.333 0 017.59.034L8.8.64a1.667 1.667 0 01.87 1.902l-.602 2.366h4.266c.737 0 1.334.597 1.334 1.333v1.403c0 .174-.034.347-.1.508l-2.064 5.01a.667.667 0 01-.616.412zM4 6.634c.242-.144.455-.337.622-.573l3.14-4.448.44.22c.141.07.214.229.175.38L7.775 4.58a1.333 1.333 0 001.292 1.662h4.266v1.403L11.44 12.24H4V6.633zM1.333 12.24V6.908h1.334v5.333H1.333z"/></svg>
                        {{ data.agree_count || 0 }}
                    </div>
                    <div
                        @click="onCategoryChange(data.category)"
                        :class="`m-topic-category`"
                        :style="`background-color: ${styles.hoverColor};color:${styles.color};`"
                    >
                        <img
                            v-svg-inline
                            class="u-icon"
                            :src="require(`@/assets/img/community/category/${styles.icon}.svg`)"
                        />
                        <div>{{ data.category }}</div>
                    </div>
                </div>
            </div>
            <div class="m-topic-box__content">
                <div class="m-topic-hot__left">
                    <a class="u-cover" :href="getPostUrl(data.id)">
                        <el-image :src="getBanner(data.banner_img)" alt="banner" srcset="" />
                    </a>
                    <div class="m-topic-imgs">
                        <a
                            :href="getPostUrl(data.id)"
                            class="u-item"
                            v-for="(item, index) in data.extra_images"
                            :key="index"
                        >
                            <el-image :src="getSquareBanner(item)" />
                        </a>
                    </div>
                </div>
                <div class="m-topic-hot__right">
                    <a :style="hightStyle" :href="getPostUrl(data.id)" class="u-title" target="_blank">
                        <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" v-bind:svg-inline="''" v-if="isTop" v-bind:srcset="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><rect width="24" height="24" rx="4" fill="#FF6969"/><path fill-rule="evenodd" fill="#FFF" d="M21 6H3V4h18v2zm-1 7.778L12 8l-8 5.778h6V21h4v-7.222h6z"/></svg>
                        <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" v-bind:svg-inline="''" v-if="data.is_star" v-bind:srcset="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><rect width="24" height="24" rx="4" fill="#FFBB69"/><path d="M21 12l-6.582-2.418L12 3 9.582 9.582 3 12l6.582 2.418L12 21l2.418-6.582L21 12z" fill="#FFF"/></svg>
                        <span>
                            {{ data.title }}
                        </span>
                    </a>
                    <div v-if="data.tags && data.tags.length" class="m-topic-tag">
                        <span v-for="(item, index) in data.tags" :key="index">
                            {{ item }}
                        </span>
                    </div>
                    <div
                        class="m-topic-collection"
                        v-if="data.collection_id && data.collection.id && data.collection.title"
                    >
                        <a :href="`/collection/${data.collection_id}`">
                            <span class="u-label">小册</span>
                            <span class="u-value">{{ data.collection.title }}</span>
                        </a>
                    </div>

                    <div class="m-topic-userInfo">
                        <a :href="authorLink(data.ext_user_info.id)" target="_blank">
                            <img class="m-topic-userInfo__avatar" :src="showAvatar()" alt="" srcset="" />
                            <span class="m-topic-userInfo__name">{{ data.ext_user_info.display_name }}</span>
                        </a>
                    </div>
                    <a :href="getPostUrl(data.id)" class="m-topic-content" target="_blank">
                        <div v-html="introduction"></div>
                    </a>
                </div>
            </div>
        </div>
        <!-- 移动端兼容置顶文章 -->
        <div class="m-topic-list m-topic-hot__mini">
            <TopicItem :data="data" />
        </div>
    </div>
</template>

<script>
import { getTimeAgo } from "@/utils/dateFormat";
import TopicItem from "@/components/community/topic_item.vue";
import { random } from "lodash";
import { __ossMirror, __imgPath, __cdn } from "@jx3box/jx3box-common/data/jx3box";
import { showAvatar, authorLink, getThumbnail } from "@jx3box/jx3box-common/js/utils";
export default {
    props: ["data"],
    inject: ["getCategoryStyle", "onCategoryChange"],
    components: {
        TopicItem,
    },
    computed: {
        hightStyle: function () {
            if (this.data.is_hight && this.data.hight_color) {
                return {
                    color: this.data.hight_color,
                };
            } else {
                return {};
            }
        },
        introduction: function () {
            const data = this.data;
            if (data.introduction) {
                if (data.introduction.length >= 200) {
                    return data.introduction + "...";
                } else if (data.introduction.length <= 0) {
                    return "......";
                } else {
                    return data.introduction;
                }
            } else {
                return "";
            }
        },
        styles: function () {
            return this.getCategoryStyle(this.data.category || "");
        },
        isTop: function () {
            return this.data.is_top || this.data.is_category_top;
        },
    },
    methods: {
        getTimeAgo,
        authorLink,
        showAvatar: function () {
            return showAvatar(this.data?.ext_user_info?.avatar);
        },
        getBanner: function (val) {
            if (val) {
                if (val.indexOf("jx3box.com") >= 0) {
                    return getThumbnail(val, [336 * 2, 176 * 2]);
                } else {
                    return val;
                }
            } else {
                // 从1-39中随机选一个
                const randomNum = random(1, 39);
                // return __imgPath + `image/banner/${appKey}${subtype}` + ".png";
                return __cdn + `design/random_cover/${randomNum}.jpg`;
            }
        },
        getPostUrl(id) {
            return `/community/${id}`;
        },
        getSquareBanner: function (val) {
            if (val.indexOf("jx3box.com") >= 0) {
                return getThumbnail(val, 48 * 2);
            }
            return val;
        },
    },
};
</script>

